@import 'src/constants.scss';

$noshadow: 0px 0px 0px rgba(0, 0, 0, 0.03);
.Header {
    position: sticky;
    overflow: hidden;
    top: 0;
    z-index: 1000;
    width: 100%;
    height: 60px;

    // box-shadow: $noshadow;
    box-shadow: 0px 15px 15px rgba(0, 0, 0, 0.03);

    background: #FFFFFFcc;
    backdrop-filter: blur(4px);

    &.show-shadow {
        transition: all .1s;
    }

    .container {

        height: 100%;
        width: 100%;
        box-sizing: border-box;
        padding: $TOP_PADDING $SIDE_PADDING;
        // background: $CLOUD_BLUE;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }

    .tumbler {
        position: relative;
        > .badge-container {

            position: absolute;
            top: 0;
            right: 0;

            transform: translateX(130%);

        }
    }

}
