.SidePane {
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0px;

    box-sizing: border-box;
    padding: 72px 0 0 0;
    overflow: scroll;

    background: white;

    transition: transform .5s;

    z-index: 500;
    &.ltr {
        left:0;
        &.hide {
            transform: translateX(-100%);
        }
    }

    &.rtl {
        right: 0;
        &.hide {
            transform: translateX(100%);
        }
    }
}
