$transition_time: .6s;
.Layout {
    width: 100%;

    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    >.starting-screen,
    >.catalog>.header {
        z-index: 1000;
    }

    >.starting-screen {
        width: 100%;
        height: 100vh;

        background: white;

        &.hide {
            display: none;
        }
    }

    >.catalog {
        width: 100%;

        &.stop-scroll {
            height: 100vh;
            overflow: hidden;
        }

        >.left-pane {
            left: 0;
            transform: translateX(-100%);
        }

        >.right-pane {
            right: 0;
            transform: translateX(100%);
        }

        >.left-pane, >.right-pane {
            transition: transform $transition_time;
            height: 100%;
            width: 100%;
            position: fixed;
            top: 0px;

            box-sizing: border-box;
            padding: 72px 0 0 0;
            overflow: scroll;

            z-index: 500;

            &.show {
                transform: translateX(0);
            }
        }


        >.header {
            position: sticky;
            top: 0;
        }

        >.body {
            width: 100%;
            min-height: 100vh;
            overflow: hidden;
            >.content {
                // transition: transform $transition_time;

                // transform: translateX(0);

                // &.move-right {
                //     transform: translateX(100%);
                // }

                // &.move-left {
                //     transform: translateX(-100%);
                // }
            }
        }
    }

    >.shopping-cart-container {
        margin-top: 100px;
        width: 100%;

        box-sizing: border-box;
        z-index: 1000;

        background: white;

        >.header {
            display: flex;
            justify-content: center;
            align-items: center;
            box-sizing: border-box;
            padding: 12px;
            height: 60px;
            position: sticky;
            top: 0;
            background: white;
            z-index: 2000;
            box-shadow: 0px 15px 15px rgba(0, 0, 0, 0.03);
        }

        &.hide {
            display: none;
        }
    }

    .checkout-form-container {
        margin-top: 100px;
        background: white;
        width: 100%;

        &.hide {
            display: none;
        }
    }
}
