.ShoppingCart {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;

    >.padding {
        padding: 24px 24px;

        >.products {
            box-sizing: border-box;
            height: 100%;
            display: flex;
            justify-content: flex-start;
            flex-direction: column;
            align-items: center;

            >.no-products {
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }

        >.footer {
            box-sizing: border-box;
            padding-bottom: 24px;
            padding-top: 24px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            &.hide {
                display: none;
            }
        }
    }


}
